<template>
    <div id="app">
        <div class="ani" v-show="animation">
          <div class="win">
            <div class="img"></div>
          <svg data-v-1c82800a="" xmlns="http://www.w3.org/2000/svg" version="1.1" class="load_ellipse"><ellipse data-v-1c82800a="" cx="26" cy="10" rx="26" ry="10" style="fill: rgb(221, 221, 221); stroke: none;"></ellipse></svg>
        </div>
          </div>
          
        <router-view/>
    </div>
</template>

<script>
import Vue from "vue";
import HeaderView from "./components/headerView/HeaderView";
import pic from "../static/img/1.jpg";
import { mapState } from "vuex";
import { setInterval, clearInterval } from "timers";
export default {
  name: "App",
  data() {
    return {
      pic: pic
    };
  },
  components: { HeaderView },
  computed: {
    ...mapState(["animation"])
  },
  created() {
    if (localStorage.user) {
      let userUrl = "http://cangdu.org:8001/v1/user";
      let params = { user_id: JSON.parse(localStorage.user).user_id };
      this.$http.get(userUrl, { params: params }).then(res => {
        if (res.data.status === undefined) {
          this.$store.commit("login");
          this.$store.commit("user", JSON.parse(localStorage.user));
        }
      });
    }
    this.change();
    // try {
    //   document.body.removeChild(document.getElementById("appLoading"));
    //   setTimeout(function() {
    //     document.getElementById("app").style.display = "block";
    //   }, 500);
    // } catch (e) {}
  },
  methods: {
    change() {
      let timer = null;
      clearInterval(timer);
      let count = 0;
      timer = setInterval(() => {
        let fontSize = parseFloat(document.documentElement.style.fontSize);
        let img = document.querySelector(".ani .img");
        count -= 0.5 * fontSize;
        img.style.backgroundPosition = "0px " + count + "px";
        if (count <= -3 * fontSize) {
          count = 50;
        }
      }, 600);
    }
  }
};
</script>

<style>
#app {
  -webkit-font-smoothing: antialiased;
}
.el-rate {
  height: 0.15rem;
}
.el-rate .el-rate__item {
  font-size: .1rem;
}
.el-rate .el-rate__item .el-rate__icon {
  font-size: 0.1rem;
  margin-right: 0;
  transform: scale(0.8);
  margin: 0 -0.01rem;
}
.el-rate .el-rate__text {
  font-size: 0.08rem;
  color: #ff6000;
}

/* 动画 */
.ani {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, .8);
  z-index: 2;
}
.win {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0.5rem;
  height: 0.5rem;
}
.ani .img {
  width: 100%;
  height: 100%;
  background: url(data:image/png;base64,);
  background-size: 0.5rem auto;
  transform: translateY(0);
  animation: load 0.6s infinite ease-in-out;
  position: relative;
  z-index: 2;
}
.ani .load_ellipse {
  position: absolute;
  width: 0.52rem;
  height: 0.4rem;
  top: 0.44rem;
  left: 0.04rem;
  z-index: 1;
  animation: ellipse 0.6s infinite ease-in-out;
}
@keyframes load {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-0.5rem);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes ellipse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}
</style>



